import * as lodash from '@microsoft/sp-lodash-subset';

/**
 * To make obj fully immutable, freeze each object in obj.
 * @remarks: this works only on pure JSON objects by design.
 * @param {obj} object to freeze
 */
/* tslint:disable:export-name */
export function deepFreeze(obj: Object): Object {
/* tslint:enable:export-name */
  if (!obj) {
    return undefined;
  }

  if (!Object.isFrozen(obj)) {
    Object.freeze(obj);
  }

  let depth: number = 0;
  const func: (o: Object) => void = (o: Object): void => {

    // Do not go more than 5 level deep
    if (++depth > 5) {
      return;
    }

    const propNames: string[] = Object.getOwnPropertyNames(o);

    propNames.forEach((name: string) => {
      const prop: Object = o[name];
      if (typeof prop === 'object' && !!prop && !lodash.isElement(prop) && !Object.isFrozen(prop)) {
        func(prop);
      }
    });
  };

  func(obj);
  return obj;
}
